.about {
  height: 100vh;
  width: 100%;
  display: inline-flex;
  /* background-color: black; */
}

.about__leftSide {
  flex: 1.5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  font-weight: 100;
}
.about__leftSide h1 {
  font-weight: 100;
  font-size: 5rem;
}
.about__menu {
  margin-top: 20px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about__menu button {
  font-size: 1.1rem;
  background-color: transparent;
  margin: 0 2px;
  padding: 10px;
  outline: none;
  cursor: pointer;
  color: gray;
  border: none;
}
.about__menu button:hover {
  color: white;
  box-shadow: 0px 5px 8px -9px rgba(245, 240, 240, 0.75);
}

.about__rightSide {
  flex: 1.5;
  display: flex;
  flex: wrap;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255);
}

.about__overView {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  /* border: 1px solid black; */
  height: 83vh;
  margin: 30px;
}
.about__heading {
  border: 1px solid white;
  margin: 20px;
  width: 80%;
}
.about__heading p {
  text-align: end;
}
.about__main {
  display: flex;
  justify-content: space-between;
  /* border: 1px solid white; */
  height: 20vh;
}
.about__mainItem {
  display: flex;
  flex: 1;
  flex: wrap;
  flex-direction: column;
  /* border: 1px solid white; */
  justify-content: space-evenly;
  align-items: center;
  height: 18vh;
}
.about__mainItem p {
  text-align: center;
  font-weight: 200;
  font-size: 1.1rem;
  width: 90%;
  height: 18%;
}

.about__education {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  /* height: 70vh; */
  overflow: hidden;
  /* border: 1px solid black; */
  margin: 20px;
  object-fit: contain;
}
.about__education ul {
  align-items: flex-start;
  /* border: 1px solid black; */
}
.about__education .MuiTimelineItem-missingOppositeContent:before {
  display: none;
}
.about__bottom {
  display: flex;
  justify-content: space-evenly;
}

.about__timelineBody {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;

  /* border: 1px solid black; */
}
.about__timelineRight {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 20px 0;
  /* border: 1px solid red; */
}
.about__top {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* border: 1px solid black; */
}
.about__education img {
  height: 80px;
  /* width: 120px; */
}
.about__workTop {
  /* border: 1px solid orange; */
  display: block;
  margin: 0 auto;
}
.about__workInfo {
  margin: 40px;
}
.about__workTop img {
  display: block;
  margin: 0 auto;
  height: 200px;
}
.about__workBottom {
  box-sizing: border-box;
  display: flex;
  justify-content: center;

  margin-bottom: 2rem;
  /* border: 1px solid orange; */
}
.about__workRow {
  display: flex;
  flex: wrap;
  padding: 0 4px;
  width: 95%;
  justify-content: center;
  /* align-items: center; */
  /* border: 1px solid orange; */
}
.about__workColumn {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
  /* border: 1px solid orange; */
}

.about__workColumn Img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
  cursor: pointer;
  opacity: 0.8;
}
.about__workColumn Img:hover {
  cursor: pointer;
  opacity: 1;
  zoom: 1.2;
}
